.suggestions-nowrap {
    white-space: nowrap
}

.suggestions-input {
    -ms-box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 100%
}

.suggestions-input::-ms-clear {
    display: none;
}

.suggestions-wrapper {
    position: relative;
    z-index: 1;
    margin: 0;
    padding: 0;
    vertical-align: top;
    -webkit-text-size-adjust: 100%
}

.suggestions-addon {
    display: none;
    opacity: 0;
    position: absolute
}

.suggestions-addon[data-addon-type=spinner] {
    background: rgba(255,255,255,.75) url(data:image/gif;base64,) 50% 50% no-repeat;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%
}

.suggestions-addon[data-addon-type=clear] {
    display: none;
}

.suggestions-addon[data-addon-type=clear]:hover {
    opacity: 1
}

.suggestions-suggestions {
    background: #fff;
    padding: 20px 0 0;
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;
    box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.07);
    border: solid 1px rgba(230, 230, 230, 1);
    background-color: rgb(255, 255, 255);
    -ms-box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    cursor: default;
    z-index: 9999;
    -webkit-text-size-adjust: 100%
}

.suggestions-suggestions strong {
    font-weight: 400;
}

.suggestions-suggestions.suggestions-mobile {
    max-width: 100%;
    left: 0!important;
}

.suggestions-suggestions.suggestions-mobile .suggestions-suggestion {
    padding: 12px 16px!important;
}

.suggestions-suggestion {
    padding: 12px 16px;
    overflow: hidden;
    cursor: pointer;
}

.suggestions-suggestion:hover {
    background-color: rgba(0, 0, 0, 0.05)
}

.suggestions-selected {
    background: #f0f0f0
}

.suggestions-selected:hover {
    background: #f0f0f0
}

.suggestions-hint {
    padding: 4px 16px;
    white-space: nowrap;
    overflow: hidden;
    font-size: 13px;
    font-weight: 400;
    font-style: normal;
    font-stretch: normal;
    line-height: 16px;
    letter-spacing: normal;
    color: rgba(0, 0, 0, 0.4);
}

.suggestions-constraints {
    list-style: none!important;
    margin: 0!important;
    padding: 0!important;
    position: absolute!important;
    white-space: nowrap!important
}

.suggestions-constraints:empty {
    display: none!important
}

.suggestions-constraints li {
    background: #f8f8f8;
    border: 1px solid #ccc;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    cursor: default;
    display: inline-block;
    margin: 0 4px 0 0;
    padding: 0 .5em
}

.suggestions-constraints li .suggestions-remove {
    height: 1em;
    width: 1em;
    display: inline-block;
    margin: 0 0 0 .25em;
    background: transparent url(data:image/svg+xml;base64,PHN2ZyB3aWR0aD0nMTAwJyBoZWlnaHQ9JzEwMCcgeG1sbnM9J2h0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnJz48Zz48cGF0aCBkPSdtMjAsMjBtMTAsMGwyMCwyMGwyMCwtMjBsMTAsMTBsLTIwLDIwbDIwLDIwbC0xMCwxMGwtMjAsLTIwbC0yMCwyMGwtMTAsLTEwbDIwLC0yMGwtMjAsLTIwbDEwLC0xMCcgZmlsbD0nIzk5OScvPjwvZz48L3N2Zz4=) 50% 50% no-repeat;
    background-size: contain;
    cursor: pointer;
    opacity: .8
}

.suggestions-constraints li .suggestions-remove:hover {
    opacity: 1
}

.suggestions-constraints li span {
    vertical-align: middle
}

.suggestions-subtext {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 16px;
    letter-spacing: normal;
    color: rgba(0, 0, 0, 0.9);
}

.suggestions-subtext_inline {
    display: inline-block;
    /*min-width: 6em;*/
    vertical-align: bottom;
    margin: 0 .5em 0 0
}

.suggestions-subtext-delimiter {
    display: inline-block;
    width: 2px
}

.suggestions-subtext_label {
    margin: 0 0 0 .25em;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    padding: 0 3px;
    background: #f5f5f5;
    font-size: 85%
}
.suggestions-value{
    display: inline-block;
    margin-bottom: 8px;
    font-size: 14px;
    font-weight: 700;
    font-style: normal;
    font-stretch: normal;
    line-height: 16px;
    letter-spacing: 0.4px;
    color: rgba(0, 0, 0, 0.9);
}
.suggestions-value strong{
    font-weight: 700;
}
.suggestions-value[data-suggestion-status=LIQUIDATED] {
    position: relative
}

.suggestions-value[data-suggestion-status=LIQUIDATED]:after {
    position: absolute;
    left: 0;
    right: 0;
    top: 50%;
    border-top: 1px solid rgba(0,0,0,.4);
    content: ""
}

.suggestions-promo,
.suggestions-promo * {
    display: none;

}

